export function statusFormatter(value, n) {
  if (value === 1) {
    return ''
  } else if (value === 2) {
    return ''
  } else if (value === 3) {
    return `(${n})`
  } else if (value === 4) {
    return `${n}`
  } else if (value === 5) {
    return `${n}`
  }
}
export function statusFormatters(value) {
  switch (value) {
    case 0:
      return '';
    case 1:
      return '';
    case 2:
      return '';
    case 3:
      return '';
    case 4:
      return '(10)';
    case 5:
      return '';
    case 6:
      return '';
    case 7:
      return '';
  }
}



