export default {
  install: function (Vue, options) {
    // isNull
    Vue.filter('isNull', function (value) {
      if (value === null || value === '') {
        return '---'
      } else {
        return value
      }
    })
    //  0 1:
    Vue.filter('status', function (value) {
      if (value === 0) {
        return ''
      } else if (value === 1) {
        return ''
      } else {
        return ''
      }
    })
    // 
    Vue.filter('reverseStatus', function (value) {
      if (value === 0) {
        return ''
      } else if (value === 1) {
        return ''
      }
    })
    //  (0: 1:)
    Vue.filter('role', function (value) {
      if (value === 0) {
        return ''
      } else if (value === 1) {
        return ''
      }
    })
    // ( 0: 1:),,0,,1
    Vue.filter('adminerStatus', function (value) {
      if (value === 0) {
        return ''
      } else if (value === 1) {
        return ''
      }
    })
    // 
    Vue.prototype.getFullTime = (t) => {
      var time = new Date(t)
      var y = time.getFullYear()
      var m = time.getMonth() + 1
      var d = time.getDate()
      var h = time.getHours()
      var mm = time.getMinutes()
      var s = time.getSeconds()
      return y + '-' + (m < 10 ? '0' + m : m) + '-' + (d < 10 ? '0' + d : d) + ' ' + (h < 10 ? '0' + h : h) + ':' + (mm < 10 ? '0' + mm : mm) + ':' + (s < 10 ? '0' + s : s)
    }
    // 
    Vue.prototype.checkPhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''))
      } else {
        if (/^1[3|4|5|7|8][0-9]\d{8}$/.test(value)) {
          callback()
        } else {
          return callback(new Error(''))
        }
      }
    }
  }
}



